import React, { Component } from 'react';

export default class Methods extends Component {
  render() {
    const openParams = `{content, [index], [config]}`;

    return (
      <table className="options">
        <thead>
          <tr>
            <th>Method</th>
            <th>Usage</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>open()</td>
            <td>ImageboxManager.open({openParams})</td>
          </tr>
          <tr>
            <td>close()</td>
            <td>ImageboxManager.close()</td>
          </tr>
        </tbody>
      </table>
    )
  }
}



